@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.about-company__title {
  margin: 0 auto 69px;
}
@media screen and (max-width: 1440px) {
  .about-company__title {
    text-align: center;
  }
}
@media screen and (max-width: 690px) {
  .about-company__title {
    margin-bottom: 40px;
  }
}
.about-company__content {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.about-company__row {
  flex: 1 2 50%;
  display: flex;
  gap: 40px;
  padding: 20px 0 5rem;
}
.about-company__row:first-child {
  border-top: 1px solid rgb(169, 169, 169);
}
.about-company__row:last-child {
  padding-bottom: 0;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  .about-company__row {
    gap: calc(20px + 20 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 1440px) {
  .about-company__row {
    gap: 20px;
  }
}
@media screen and (max-width: 690px) {
  .about-company__row {
    flex-direction: column;
  }
}
@media screen and (max-width: 690px) {
  .about-company__row h3 {
    margin-bottom: 10px;
  }
}
.about-company__row p {
  font-size: 16px;
  font-weight: 400;
  line-height: 125%; /* 20px/16 */
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .about-company__row p {
    font-size: calc(12px + 4 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .about-company__row p {
    font-size: 12px;
  }
}
.about-company__row:not(:last-child) {
  border-bottom: 1px solid rgb(169, 169, 169);
}
.about-company__item {
  flex: 0 1 50%;
}
.about-company__text {
  color: rgb(0, 0, 0);
  font-family: Averta Demo PE Cutted Demo;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0%;
  text-align: left;
}